body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

header {
  background: url("https://s3.us-east-2.amazonaws.com/davidrzuluaga.com/assets/backgrounds/DRZpic(mobile).jpg") no-repeat 50%;
  background-size: cover;
  color: white;
  height: 97vh; }
  header .name {
    position: relative;
    top: 16%;
    margin: 0 5%; }

.skills #cvlink {
  margin-left: 60%; }

header h1 {
  text-shadow: -2px 2px 5px #000; }

header p {
  font-size: 21px;
  font-weight: 300;
  text-shadow: -1px 1px 5px #000; }

.link-button:hover, .link-button:focus {
  text-decoration: none; }

.hidden {
  visibility: hidden; }

.description {
  background: url("https://s3.us-east-2.amazonaws.com/davidrzuluaga.com/assets/backgrounds/NYC-path.jpg") no-repeat 50%;
  background-size: cover;
  height: auto; }
  .description p {
    color: white;
    position: relative;
    margin: 0 10% 0 14%;
    font-size: 21px;
    text-shadow: 2px 3px 5px #000;
    padding-bottom: 10px; }
    .description p:first-child {
      padding-top: 15%; }
    .description p:last-child {
      padding-bottom: 15%; }

.imgskill {
  margin: 1% 1% 1% 1%; }

.skills {
  background: url("https://s3.us-east-2.amazonaws.com/davidrzuluaga.com/assets/backgrounds/skills.jpg") no-repeat 50%;
  background-size: cover;
  height: auto;
  padding-bottom: 2%; }

.skill {
  padding-top: 50px;
  margin: 0 5%;
  color: white;
  font-size: 18px;
  text-shadow: 2px 3px 5px #000; }

.langSkills {
  display: inline-block;
  margin: 5px 20px 0px 0; }
  .langSkills p {
    padding: 5px 0 0 0;
    text-align: center; }

.navbar-brand:hover {
  cursor: pointer; }

.portfolio {
  background-color: #e0e0e0;
  background-size: cover;
  height: auto; }

.imgportfolio {
  height: 100%;
  overflow: hidden;
  position: relative; }

.pages {
  padding-top: 2%;
  padding-bottom: 2%;
  color: black;
  position: relative;
  margin: 0 5%;
  font-size: 18px; }

.experience {
  background: url("https://s3.us-east-2.amazonaws.com/davidrzuluaga.com/assets/backgrounds/Summit.JPG") no-repeat 50%;
  background-size: cover;
  height: auto;
  padding-bottom: 2%; }

.xp {
  padding-top: 50px;
  margin: 0 5%;
  color: white;
  font-size: 18px;
  text-align: left; }
  .xp .xpcard {
    background-color: rgba(0, 94, 170, 0.726);
    border-color: #3d3d3d;
    color: white; }
  .xp h1 {
    color: #000000;
    text-shadow: 1px 1px 2px white;
    padding: 11px 10px;
    font-size: 44px; }
  .xp h2 {
    margin-top: 5px;
    text-shadow: 1px 1px 2px black; }
  .xp a {
    color: white; }
    .xp a:hover {
      text-decoration: none;
      color: #bebebe; }
  .xp img {
    height: 100%;
    overflow: hidden;
    position: relative; }

.contact {
  background-color: #a3a3a3;
  background-size: cover;
  height: auto;
  padding-bottom: 2%; }

.contactinfo {
  padding-top: 2%;
  padding-bottom: 2%;
  margin: 0 5%;
  font-size: 18px;
  font-weight: 300; }

.social {
  background-color: #535353;
  text-align: center;
  padding: 1% 5%; }
  .social a {
    color: white;
    display: block;
    padding: 2px 0; }
    .social a:hover {
      text-decoration: none;
      color: #7e7e7e; }

@media (min-width: 930px) {
  header {
    background: url("https://s3.us-east-2.amazonaws.com/davidrzuluaga.com/assets/backgrounds/DRZpic.jpg") no-repeat 50%;
    background-size: cover;
    color: white;
    height: 100vh; }
    header .name {
      position: relative;
      top: 43%;
      margin-left: 20%; }
  .social a {
    display: inline; }
  .skills #cvlink {
    margin-left: 80%; }
  .description p {
    position: relative;
    top: 20%;
    margin-left: 20%;
    margin-right: 30%;
    font-size: 21px;
    font-weight: 300;
    text-shadow: 2px 3px 5px #000; } }

